 sàng vào môi trường mới không bị bỡ ngỡ và lạ lẫm, Chương\n                        trình Hành trang vào lớp 1 giúp trẻ tiếp cận với các hình thức, phương pháp học tập trong nhà trường, là bước chuyển giao\n                        nhẹ nhàng cho Trẻ từ mầm non lên tiểu học.\n                      </div></div></div></div></div> <div class=\"prog-item full-item\" data-v-80afe500><div class=\"prog-row\" data-v-80afe500><div class=\"prog-col\" data-v-80afe500><div class=\"prog-img\" data-v-80afe500><div class=\"prog-img-pd\" data-v-80afe500><img width=\"840\" height=\"560\" src=\"/images/new-image/3.jpg\" alt data-lazy-srcset=\"https://ischool.vn/wp-content/uploads/2022/10/TDH_3478-840x560.jpg 840w, https://ischool.vn/wp-content/uploads/2022/10/TDH_3478-300x200.jpg 300w, https://ischool.vn/wp-content/uploads/2022/10/TDH_3478-1024x683.jpg 1024w, https://ischool.vn/wp-content/uploads/2022/10/TDH_3478-768x512.jpg 768w, https://ischool.vn/wp-content/uploads/2022/10/TDH_3478-1536x1024.jpg 1536w, https://ischool.vn/wp-content/uploads/2022/10/TDH_3478-2048x1365.jpg 2048w, https://ischool.vn/wp-content/uploads/2022/10/TDH_3478-50x33.jpg 50w, https://ischool.vn/wp-content/uploads/2022/10/TDH_3478-270x180.jpg 270w, https://ischool.vn/wp-content/uploads/2022/10/TDH_3478-scaled.jpg 1920w\" data-lazy-sizes=\"(max-width: 840px) 100vw, 840px\" data-lazy-src=\"https://ischool.vn/wp-content/uploads/2022/10/TDH_3478-840x560.jpg\" class=\"attachment-960x560 size-960x560\" data-v-80afe500></div></div></div> <div class=\"prog-col\" data-v-80afe500><div class=\"prog-content\" data-v-80afe500><h3 data-aos=\"fade-up-cus\" class=\"t-title prog-name mb-32\" data-v-80afe500>ĐẶC ĐIỂM CHƯƠNG TRÌNH</h3> <div data-aos=\"fade-up-cus\" data-aos-delay=\"300\" class=\"text mb-32\" data-v-80afe500>\n                        Những kỹ năng quan trọng giúp Trẻ bỏ túi thần kì để Trẻ tự tin vững vàng cắp sách tới trường, để mỗi ngày đi học là mỗi ngày\n                        hữu ích <br data-v-80afe500> <strong class=\"ds-title\" data-v-80afe500>Luyện viết chuẩn</strong>: Hướng dẫn tư thế ngồi, cách để vở, tay cầm bút đúng chuẩn; Trình bày,\n                        luyện viết trên vở và bảng con. <br data-v-80afe500> <strong class=\"ds-title\" data-v-80afe500>Đọc thông thạo</strong>: Dạy đọc thành thạo 29 chữ cái theo cách thực hành, phát âm đúng chuẩn SGK\n                        mới. <br data-v-80afe500> <strong class=\"ds-title\" data-v-80afe500>Ghép nhóm chữ</strong> : Biết đọc và ghép vần đơn giản. <br data-v-80afe500> <strong class=\"ds-title\" data-v-80afe500>Học toán cơ bản</strong>: Nhân biết các số, cộng trừ trong phạm vi 10, nhận biết dấu lớn, dấu bé,\n                        dấu bằng, so sánh các số trong phạm vi 10.\n                      </div></div></div></div></div> <div class=\"prog-item full-item\" data-v-80afe500><div class=\"prog-row\" data-v-80afe500><div class=\"prog-col\" data-v-80afe500><div class=\"prog-img\" data-v-80afe500><div class=\"prog-img-pd\" data-v-80afe500><img width=\"839\" height=\"560\" src=\"/images/co-ban-2.jpg\" alt data-lazy-srcset=\"https://ischool.vn/wp-content/uploads/2022/08/96372450_938517509916778_8547025420581601280_n-839x560.jpg 839w, https://ischool.vn/wp-content/uploads/2022/08/96372450_938517509916778_8547025420581601280_n-300x200.jpg 300w, https://ischool.vn/wp-content/uploads/2022/08/96372450_938517509916778_8547025420581601280_n-1024x684.jpg 1024w, https://ischool.vn/wp-content/uploads/2022/08/96372450_938517509916778_8547025420581601280_n-768x513.jpg 768w, https://ischool.vn/wp-content/uploads/2022/08/96372450_938517509916778_8547025420581601280_n-1536x1026.jpg 1536w, https://ischool.vn/wp-content/uploads/2022/08/96372450_938517509916778_8547025420581601280_n-50x33.jpg 50w, https://ischool.vn/wp-content/uploads/2022/08/96372450_938517509916778_8547025420581601280_n-270x180.jpg 270w, https://ischool.vn/wp-content/uploads/2022/08/96372450_938517509916778_8547025420581601280_n.jpg 1920w\" data-lazy-sizes=\"(max-width: